.App {
  text-align: center;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 2s linear;
  }
}

.App-header {
  justify-content: center;
  color: black;
  margin-bottom: 50px;
}

.App-link {
  border: 2px solid black;
  border-radius: 10px;
  padding: 10px 30px;
  font-size: 1rem;
  cursor: pointer;
}

.App-link:hover {
  background-color: black;
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-grid-container {
  display: grid;
  gap: 20px;
  grid-template-columns: 20% 20% 20% 20%;
  padding: 10px;
  justify-content: center;
  margin: 10px 0;
}

.App-grid-item {
  border: 5px solid rgba(0, 0, 0, 0.8);
  padding: 20px;
  font-size: 1rem;
  text-align: left;
  border-radius: 10px;
  box-shadow: 5px 5px 10px 3px gray;
  cursor: pointer;
}

.App-grid-item:hover {
  box-shadow: none;
}

/* Mobile version */
@media screen and (max-width: 480px) {
  .App-grid-container {
    display: grid;
    gap: 20px;
    grid-template-columns: auto;
    padding: 10px;
    justify-content: center;
    margin: 10px 20px;
  }
  
  .App-grid-item {
    border: 5px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 1rem;
    text-align: left;
    border-radius: 10px;
    box-shadow: 5px 5px 10px 3px gray;
    cursor: pointer;
  }
}

@media screen and (max-width: 768px) and (min-width: 481px) {
  .App-grid-container {
    display: grid;
    gap: 20px;
    grid-template-columns: 40% 40%;
    padding: 10px;
    justify-content: center;
    margin: 10px 20px;
  }
  
  .App-grid-item {
    border: 5px solid rgba(0, 0, 0, 0.8);
    padding: 20px;
    font-size: 1rem;
    text-align: left;
    border-radius: 10px;
    box-shadow: 5px 5px 10px 3px gray;
    cursor: pointer;
  }
}
